  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

ul {
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

/****** screen-reader only class ******/
.sr-only {
  position:absolute;
  left:-10000px;
  top:auto;
  width:1px;
  height:1px;
  overflow:hidden;
  }

.button__primary {

  box-shadow: 2px 4px 6px 1px rgba(0, 0, 0, 0.13);

  display: inline-block;
  padding: 1.46em 2em;
  border: 0.1em solid #772ADB;
  border: 0.1em solid var(--primary);
  margin: 0 0.2em 0.2em 0;
  border-radius: 0.22em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  font-size: large;
  color: #772ADB;
  color: var(--primary);
  text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
  background-color: #FFFFFF;
  text-align: center;
  transition: all 0.15s;
  cursor: pointer;
}

.button__primary:hover {
  text-shadow: 0 0 2em rgba(255, 255, 255, 1);
  color: #FFFFFF;
  color: white;
  background-color: #772ADB;
  background-color: var(--primary);
}

.shadow__low {
  box-shadow: -5px 13px 15px -4px rgba(0, 0, 0, 0.06);
}

.shadow__up {
  box-shadow: -1px -9px 19px 1px rgba(0, 0, 0, 0.06);
}

/****** center elements horizzontally in a div ******/
.presentation__center {
  margin: auto;
  justify-content: center;
  text-align: center;
}

/****** center elements vertically in a div ******/
.presentation__verticalcenter {
  display: flex;
  align-items: center;
}

/****** make a div with 100% display height and display width ******/
.presentation__fulldisplay {
  width: 100vw;
  height: 100vh;
}

