    /* background: linear-gradient(0deg, rgba(119, 42, 219, 0.7), rgba(119, 42, 219, 0.7)), 
    url("../assets/homeimage3.png") 0 0 no-repeat; */
    overflow: hidden;
  }
  
.wrapper:hover  {
    box-shadow: 0px 0px 23px 3px rgba(0,0,0,0.33);
    text-decoration: underline;
}

.wrapper:hover .content  {
    text-decoration: underline;
}
  .content {
    position: absolute;
    z-index: 10;
    padding: 15px;
    box-sizing: border-box;
    font-size: 24px;
    color: white;

  }

@media all and (max-width:770px) {
    .wrapper {
        position: relative;
        height: 30vh;
        width: 80vw;
        margin-bottom: 3vh;
        margin-top: 3vh;
    }
}

  
