/******* HERO ********/
.hero {

  position: relative;
  width: 100vw;
  height: 100vh;
  left: 10%;
  top: 10%;
}

.hero__title {
  color: var(--primary);
  /* ‚àèfont-size: 7em; */
  margin-block-end: -0.6em;
}

.subtitle {
  font-weight: bold;
  --submin: 23px;
  --submax: 38px;
  font-family: "Hind";
  font-style: normal;
  line-height: 77px;
  padding-top: 3%;
  
}
/* Give to subtitle a fluid typohraphy */

@media screen and (min-width: 320px) {
  .subtitle  {
      font-size: calc(var(--submin) + 6 * ((100vw - 320px) / 680));
  }
}

@media screen and (min-width: 1000px) {
  .subtitle  {
      font-size: var(--submax);
  }
}

