/******* SERVICES ********/
.services {
  position: relative;
  width: 100vw;
  height: 100vh;
  left: 10%;
  top: 10%;
}


li{
  list-style: none;
}



.services__list {
  /* box-shadow: -3px 10px 8px 9px rgba(0, 0, 0, 0.06); */
  margin-left: -30px;
  margin-top: -30px;
  padding-bottom: 70px;
  padding-top: 2px;
  border-radius: 20px;
  padding-inline-start: 5%;
  overflow: hidden;
}


.services__image2 {
  width: 35vh;
  /* left: 72vw;
  bottom: 23vh; */
  border-radius: 12px;
  box-shadow: -3px 10px 8px 9px rgba(0, 0, 0, 0.06);

}

/******* PRESENTATION ********/

.presentation__text {
  padding: 0 20% 0 20%;
  margin-top: 2%;
}

#artrow {
  margin-top: 4%;
}

#cta__presentation {
  margin-top: 2em;
}

.presentation_art {
  border-radius: 8px;
}

/******* CONTACT SECTION ********/

.contact { 
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.contact__textbox {
  color: white;
  background-color: var(--primary);
  margin-left: 0%;    
  padding: 2% 6%                   !important;
  border-radius: 30px;
  transform: translateX(+6%);
  /* !important added in order to overrule the react-flexbox-grid imposed padding  */
}

.contact__text {
  margin-block-end: 50px;
}

/******* Media Query for responsivness ********/


@media all and (max-width:600px) {
  .hero__image {
    width: 0vh;
    height: 0vh;
    left: 55vw;
    top: 5vh;
    z-index: -1;
    box-shadow: -12px 18px 0px 3px rgba(119,60,201,0);
  }
    .contact__textbox {
    padding: 6% 6%                   !important;
  }

  .services {
    position: relative;
    width: 80vw;
    height: 100vh;
   
  }
  
  body {
    overflow-y: hidden;
  }
  
  .services__list {
    box-shadow: -3px 10px 8px 9px rgba(0, 0, 0, 0.06);
    padding: 10% 10%;
    padding-bottom: 70px;
    padding-top: 2px;
    border-radius: 20px;
  }

  .services__image2 {
    width: 20vh;
  }
  
}